<template>
  <div class="gc">
       <div
      class="fruit"
      v-show="indexs == 4"
      v-for="(item, index) in fruite"
      :key="index"
      @click="pidxq(item.pid)"
    >
      <div class="frfp">
        <img :src="item.largeImg" alt="" />
      </div>
      <div class="frmz">{{ item.name }}</div>
      <div class="fryw">{{ item.enname }}</div>
      <div class="frprice">￥{{ item.price }}</div>
      <div class="frtype">{{item.typeDesc}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Gc",
  data() {
    return {
      indexs: 4,
      fruite: [],
    };
  },

  created() {
      this.fruit();
  },

  methods: {
    fruit() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: this.baseUrl + "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "type",
          value: "fruit_tea",
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == 500) {
            this.fruite = res.data.result;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    pidxq(pid) {
      this.$router.push({ name: "Detail", params: { pid } });
    },
  },
};
</script>

<style lang="less" scoped>
.gc{
    .fruit {
    position: relative;
    width: 95%;
    margin-left: 8px;
    margin-top: 15px;
    background: white;
    height: 100px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    float: left;
    .frfp {
      width: 80px;
      height: 80px;
      margin-top: 10px;
      margin-left: 10px;
      float: left;
      img {
        width: 80px;
        height: 80px;
      }
    }
    .frmz {
      padding-top: 30px;
      padding-left: 100px;
      padding-bottom: 5px;
      font-size: 16px;
    }
    .fryw {
      width: 150px;
      margin-left: 100px;
      white-space: nowrap;
      overflow: hidden;
      font-size: 14px;
    }
    .frprice {
      float: right;
      margin-right: 20px;
      color: #f8d262;
      font-size: 18px;
    }
    .frtype{
          width: 55px;
          height: 45px;
          line-height: 45px;
          text-align: center;
          background: chocolate;
          color: white;
          font-size: 16px;
          position: absolute;
          top: 0px;
          left: 290px;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
        }
  }
}
</style>