<template>
  <div class="natie">
    <div
      class="naties"
      v-show="indexs == 2"
      v-for="(item, index) in naties"
      :key="index"
      @click="pidxq(item.pid)"
    >
      <div class="ntp">
        <img :src="item.largeImg" alt="" />
      </div>
      <div class="ntmz">{{ item.name }}</div>
      <div class="ntyw">{{ item.enname }}</div>
      <div class="ntprice">￥{{ item.price }}</div>
      <div class="nttype">{{item.typeDesc}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Natie",
  data() {
    return {
        indexs:2,
        naties:[]
    };
  },

  created () {
      this.natie();
  },

  methods: {
    //拿铁
    natie() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: this.baseUrl + "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "type",
          value: "latte",
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == 500) {
            this.naties = res.data.result;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    pidxq(pid){
        this.$router.push({name:"Detail", params:{pid}})
    }
  },
};
</script>

<style lang="less" scoped>
.natie {
    padding-bottom: 810px;
  .naties {
    position: relative;
    width: 95%;
    margin-left: 8px;
    margin-top: 15px;
    background: white;
    height: 100px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    float: left;
    .ntp {
        width: 80px;
        height: 80px;
        margin-top: 10px;
        margin-left: 10px;
        float: left;
      img {
        width: 80px;
        height: 80px;
      }
    }
    .ntmz {
      padding-top: 30px;
      padding-left: 100px;
      padding-bottom: 5px;
      font-size: 16px;
    }
    .ntyw {
      width: 150px;
      margin-left: 100px;
      white-space: nowrap;
      overflow: hidden;
      font-size: 14px;
    }
    .ntprice {
      float: right;
      margin-right: 20px;
      color: #f8d262;
      font-size: 18px;
    }
    .nttype{
          width: 55px;
          height: 45px;
          line-height: 45px;
          text-align: center;
          background: chocolate;
          color: white;
          font-size: 16px;
          position: absolute;
          top: 0px;
          left: 290px;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
        }
  }
}
</style>