<template>
  <div class="caidan">
    <div class="hear">
      <van-search  v-model="keywords"  @focus="focus" placeholder="输入商品名称" shape="round" />
       <div class="cross" @click="dj">X</div>
    </div>

    <div class="dl">
      <div class="ls"  @click="visd(item.pid)" v-for="(item,index) in sousuo1()" :key="index">
      <div class="stp">
        <img :src="item.largeImg">
      </div>
      <div class="smz">{{item.name}}</div>
      <div class="syw">{{item.enname}}</div>
      <div class="sprice">{{item.price}}</div>
    </div>
    </div>

    <div class="box">
      <div class="xxk">
        <ul class="xxk2">
          <li :class="{ curren: indexs == 1 }" @click="indexs = 1">
            <img src="../assets/img/icon_ddpxycuvvt4/naicha.png" alt="" />
            <div>推荐</div>
          </li>
          <li :class="{ curren: indexs == 2 }" @click="indexs = 2">
            <img src="../assets/img/icon_ddpxycuvvt4/canyinicon-.png" alt="" />
            <div>拿铁</div>
          </li>
          <li :class="{ curren: indexs == 3 }" @click="indexs = 3">
            <img src="../assets/img/icon_ddpxycuvvt4/natie.png" alt="" />
            <div>咖啡</div>
          </li>
          <li :class="{ curren: indexs == 4 }" @click="indexs = 4">
            <img src="../assets/img/icon_ddpxycuvvt4/canyinicon-.png" alt="" />
            <div>水果茶</div>
          </li>
        </ul>
        <!-- 推荐 -->
        <transition-group enter-active-class="animated slideInDown">
        <div
          v-show="indexs == 1"
          class="tuijian"
          v-for="(item, index) in hotsps"
          :key="index"
          @click="visd(item.pid)"
        >
          <div class="qbsp" >
            <div class="qbtp">
              <img class="tps" :src="item.largeImg" />
            </div>
            <div class="d1">{{ item.name }}</div>
            <div class="d2">{{ item.enname }}</div>
            <div class="d3">￥{{ item.price }}</div>
            <div class="d4">{{item.typeDesc}}</div>
          </div>
        </div>
        </transition-group>

        <!-- 拿铁 -->
        <transition enter-active-class="animated wobble">
          <Naties v-show="indexs==2"></Naties>
        </transition>

        <!-- 咖啡 -->
        <transition enter-active-class="animated zoomIn">
          <Kafeis v-show="indexs==3"></Kafeis>
        </transition>

        <!-- 茶 -->
        <transition enter-active-class="animated rotateInDownLeft">
          <Fruits v-show="indexs==4"></Fruits>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import Naties from '../components/Natie'
import Kafeis from '../components/Kafei'
import Fruits from '../components/Shuiguocha'
export default {
  name: "Caidan",
  data() {
    return {
      indexs: 1,

      hotsps: [],

      keywords:'',

      sera:[],
    };
  },
  components: {
    Naties,
    Kafeis,
    Fruits
  },
  created() {
    this.feilei();
    this.shousuo();
  },

   computed: {
      Search(){
          if(this.keywords){
              return this.sera.filter((value)=>{
                  return value.name.includes(this.keywords)
              })
          }
           
      },
  },

  methods: {
    //推荐
    feilei() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: this.baseUrl + "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == 500) {
            this.hotsps = res.data.result;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

     visd(pid){
          this.$router.push({name:"Detail", params:{pid}})
        },

          //  搜索
    shousuo(keywords) {
        // 
        this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: this.baseUrl + "/search",
        params: {
          appkey: this.appkey,
          name:this.keywords
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == "Q001") {
            this.sera = res.data.result;

          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    sousuo1(){
        if(!this.keywords){
            return this.sera;
            
        }
        return this.Search;
        
    },

    focus(){
          let dl = document.querySelector(".dl")
          dl.style.display = "block"
  
    },


    dj(){
      let dl = document.querySelector(".dl")
          dl.style.display = "none"
    }
  },
};
</script>

<style lang="less" scoped>
.caidan {
  /deep/.van-icon-search::before {
    content: "\F0AF";
    color: #ffff32;
  }
  .cross{
    width: 12px;
    height: 12px;
    background: gray;
    color: white;
    border-radius: 50%;
    position: absolute ;
    font-size: 8px;
    line-height: 12px;
    text-align: center;
    top: 20px;
    right: 25px
  }
  .dl{
    display: none;
    position: absolute;
    top: 55px;
    left: 9px;
    z-index: 5;
    width: 95%;
    // height: 600px;
    // background: rgba(250,250,250,0.6);
    
    .ls{
      width: 118px;
      height: 118px;
      float: left;
      background: rgba(250,250,250,1);
    .stp{
      margin-left: 35px;
      margin-top: 15px;
      width: 45px;
      height: 45px;
      img{
        width: 45px;
        height: 45px;
      }
    }
    .smz{
      margin-left: 35px;
    }
    .syw{
      width: 75px;
      height: 15px;
      white-space:nowrap;
      overflow: hidden;
      margin-left: 35px;

    }
    .sprice{
      margin-left: 35px;
      color: red;
    }
  }
  }
  
  .xxk {
    .xxk2 {
      width: 100%;
      background: #fbfafa;
      height: 75px;
      display: flex;
      li {
        width: 30px;
        height: 30px;
        text-align: center;
        float: left;
        flex: 1;
        margin-top: 15px;
        img {
          width: 30px;
          height: 30px;
        }
      }
      .curren {
        color: yellow;
      }
    }
    .tuijian {
      width: 95%;
      height: 100px;
      margin-left: 8px;
      margin-top: 10px;
      background: white;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      .qbsp {
        position: relative;
        .qbtp {
          width: 80px;
          height: 80px;
          float: left;
          margin-top: 10px;
          margin-left: 10px;
          
          .tps {
            width: 80px;
            height: 80px;
          }
        }
        .d1{
          padding-top: 30px;
          padding-left: 100px;
          padding-bottom: 5px;
          font-size: 16px;
        }
        .d2{
          width: 150px;
          margin-left: 100px;
          white-space:nowrap;
          overflow: hidden;
          font-size: 14px;
        }
        .d3{
          float:right;
          margin-right: 20px;
          color: #f8d262;
          font-size: 18px;
        }
        .d4{
          width: 55px;
          height: 45px;
          line-height: 45px;
          text-align: center;
          background: chocolate;
          color: white;
          font-size: 16px;
          position: absolute;
          top: 0px;
          left: 290px;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
        }
      }
    }

  }
}
</style>